import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { HttpClient } from '@angular/common/http';
//import { Observable } from 'rxjs/Observable';
import { Router } from '@angular/router'

@Injectable()
export class DataService {

  constructor(public http:HttpClient, private _router: Router) { 
 
  }

  public deleteMessage(id : string){
    this.http.delete('http://10.0.2.2:3000/messages/' + id).subscribe((data) =>
    console.log('Message deleted'));
  }

  public getUsers(){
    return this.http.get('http://10.0.2.2:3000/user');
          };

  public getGames(){
    return this.http.get('http://10.0.2.2:3000/games');
          };
  
  public getAdmin(){
    return this.http.get('http://10.0.2.2:3000/user' )
  }       

  public ranking(username: string){
    return this.http.get('http://10.0.2.2:3000/user/' + username)
  }

  public userDelete(id: string): void{
    this.http.delete('http://10.0.2.2:3000/user/' + id).subscribe((data) =>
    console.log('User deleted'));
  }

  public userWin(id: string): void{
    this.http.patch('http://10.0.2.2:3000/user/' + id + "/win", true).subscribe((data) =>
    console.log('Win added'));
  }

  public userLoss(id: string): void{
    this.http.patch('http://10.0.2.2:3000/user/' + id + "/loss", true).subscribe((data) =>
    console.log('Loss added'));
  }

  public userPlay(id: string, check: boolean): void{
    if(check){
    this.http.patch('http://10.0.2.2:3000/user/' + id + "/playing", true).subscribe((data) =>
    console.log('user in game'));
    }else{
      this.http.patch('http://10.0.2.2:3000/user/' + id + "/notPlaying", true).subscribe((data) =>
    console.log('user not in game'));
    }
  }

  public makeAdmin(id: string): void{
    this.http.patch('http://10.0.2.2:3000/user/' + id + "/makeAdmin", false).subscribe((data) =>
    console.log('admin added'));
  }

  public unmakeAdmin(id: string): void{
    this.http.patch('http://10.0.2.2:3000/user/' + id + "/unmakeAdmin", true).subscribe((data) =>
    console.log('admin removed'));
  }

  public createGame(game){
    return this.http.post<any>("http://10.0.2.2:3000/games" , game)
  }

  public gameDelete(code : string){
    return this.http.delete('http://10.0.2.2:3000/games/' + code).subscribe((data) =>
    console.log('Game removed from list'));
  }


  public getPlay(id: string){
    return this.http.get('http://10.0.2.2:3000/user/' + id + '/status');
  }

 
  public getMessages(username : string){
    return this.http.get('http://10.0.2.2:3000/messages/' + username);
  }

  
  public isValid(username : string, callback: Function): any{
    this.http.get<any>("http://10.0.2.2:3000/user/" + username + "/check").subscribe((data: any) => {
      callback(data);
    }) 
  }
} 

  