import { Component, OnInit } from '@angular/core';
import{ AuthService} from 'src/app/services/auth.service';
import { DataService } from 'src/app/services/data.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  me: any = {
    score: 0,
    id: '',
    username: ''
  };
  
  test: boolean;
  interval: any;

  constructor(private authService: AuthService,
    public dataService : DataService) { }

  ngOnInit() {

    this.me.username= this.authService.getUsername();
    this.me.id = this.authService.getId();

    this.dataService.userPlay(this.me.id, false)

    setTimeout(()=>{
      this.dataService.getPlay(this.me.id).subscribe((data: any) => {
        this.test = data.user.inGame;
    })
    },50);
  }
}
