import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import * as JWT from "jwt-decode"

@Injectable()
export class AuthService {


  constructor(private http: HttpClient,
              private _router: Router) { }

  registerUser(user) {
    return this.http.post<any>("http://10.0.2.2:3000/user/signup", user)
  }

  postMessage(message){
    return this.http.post<any>("http://10.0.2.2:3000/messages", message)
  }

  loginUser(user) {
    return this.http.post<any>("http://10.0.2.2:3000/user/login", user)
    
  }

  logoutUser() {
    localStorage.removeItem('token')
    this._router.navigate(['/'])
  }

  getToken() {
    return localStorage.getItem('token')
  }

  loggedIn() {
    return !!localStorage.getItem('token')    
  }

  getId() : string {
    const token = <any>JWT(this.getToken());
    return token.userId
  }

  getUsername() {
    const token = <any>JWT(this.getToken());
    return token.username
  }

  getEmail() {
    const token = <any>JWT(this.getToken());
    return token.email
  }

  isAdmin() : boolean {
    const token = <any>JWT(this.getToken());
    if (token.isAdmin == true){
    return true }
    else {return false}
    }
  }