import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { UserSignupComponent } from '../user-signup/user-signup.component';
import { AuthService } from 'src/app/services/auth.service';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styleUrls: ['./user.component.css']
})
export class UserComponent /*implements OnInit*/ {
  users: IUser[];
  visible: Boolean[];
  len: number;
  done: boolean = false;
  searchUsername: string = "";
  searchedUsername: string = "";
  me: boolean = false;
  searched: any;
  
  

  constructor(public dataService: DataService, private authService: AuthService) {
 
  }


  ngOnInit(): void {
    this.dataService.getUsers().subscribe((data: any) => {
      this.users = data.user;
    })
  }

  search(username:string){
    this.dataService.ranking(username).subscribe((data: any) => {
    this.searchedUsername = username;
    this.searched = data.user;
    if(this.authService.getUsername()==username){
      this.me=true
    }else{
      this.me =false
    }
    })
    return;
  }

  myRank(){
    this.search(this.authService.getUsername())
  }

  sortTable(n) {
    var table, rows, switching, i, x, y, shouldSwitch, dir, switchcount = 0;
    table = document.getElementById("myTable");
    switching = true;
    dir = "asc"; 
    while (switching) {
      switching = false;
      rows = table.rows;
      for (i = 1; i < (rows.length - 1); i++) {
        shouldSwitch = false;
        x = rows[i].getElementsByTagName("TD")[n];
        y = rows[i + 1].getElementsByTagName("TD")[n];
         if (dir == "desc") {
          if (Number(x.innerHTML) < Number(y.innerHTML)) {
              shouldSwitch = true;
              break;
          }
        }
      }
      if (shouldSwitch) {
        rows[i].parentNode.insertBefore(rows[i + 1], rows[i]);
        switching = true;
        switchcount ++; 
      } else {
        if (switchcount == 0 && dir == "asc") {
          dir = "desc";
          switching = true;
        }
      }
    }
  }

}

export interface IUser {
  id: string,
  username: string,
  email: string,
  win: number,
  losses: number,
  isAdmin: boolean,
  inGame: boolean,
  password: string

}

