import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';
import { HttpClient } from '@angular/common/http';
import { AuthService } from '../../services/auth.service';
import { NgStyle } from '@angular/common';

@Component({
  selector: 'app-messages',
  templateUrl: './messages.component.html',
  styleUrls: ['./messages.component.css']
})
export class MessagesComponent implements OnInit {

  constructor(public dataService: DataService, public authService: AuthService) { }
  messages: IMessage[];

  user: string = this.authService.getUsername();
  messageData = <any>{};
  inputVar : string = '';
  todayDate = new Date();
  dateToday = (this.todayDate.getHours() + ':' + this.todayDate.getMinutes() + ' ' 
              + this.todayDate.getDate() + '-' + ((this.todayDate.getMonth() + 1)) + '-' + this.todayDate.getFullYear());
  

  ngOnInit() {

    this.dataService.getMessages(this.user).subscribe((data: any) => {
      this.messages = data.message;
    })
  }

  delete(id: string){
    console.log("message deleted");
    this.dataService.deleteMessage(id);
    location.reload();
  }

  postMessage() {
    this.dataService.isValid(this.messageData.recipient, (isValid: boolean) => {
      if (isValid) {
        this.messageData.date = this.dateToday;
        this.messageData.author = this.authService.getUsername();
        this.authService.postMessage(this.messageData)
          .subscribe(
            res => {
              console.log(this.messageData)
              location.reload();
            },
            err => console.log(err)
          )
      }
      else {
        console.log("Recipient not found");
        alert('User does not exist');
      }
    });
  }

}

interface IMessage {
  id: string,
  content: string,
  author: string,
  recipient: string,
  date: string
}
